import { Injectable } from '@angular/core';
import { Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
//import { environment } from './../environments/environment'


@Injectable()
export class HttpUtilService {

  //  private API_URL = environment.pathBase;

   // url(path: string) {
  //      return this.API_URL + path;
 //   }

    headers() {
        let headersParams = { 'Content-Type': 'application/json' };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }

    Headers(apikey: string) {
        let headersParams = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*', 'apikey': apikey };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }

    HeadersSegundaVia(apikey: string, vencimento: Date) {
        let headersParams = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*', 'apikey': apikey, 'novoVencimento': vencimento };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }
    HeadersParceiro(apikey: string, tipoParceiro: string) {
        let headersParams = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*', 'apikey': apikey, 'parceiro': tipoParceiro };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }

   /* HeaderAgendamento(apikey: string, filtro: Filtro, flag: string) {

        let headersParams = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*', 'apikey': apikey, 'tipo': filtro.tipo };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }*/

    HeaderLogin(apikey: string, login: string) {
        let headersParams = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*', 'apikey': apikey, 'login': login };
        let headers = new Headers(headersParams);
        let options = new RequestOptions({ headers: headers });
        return options;
    }


    extrairDados(response: Response) {
        let data = response.json();
        return data || {};
    }

    processarErros(error: Response) {
        return Observable.throw('Erro acessando servidor remoto. ' + error.statusText);
    }
}


