import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RouterLink, } from '@angular/router';
import { Router } from '@angular/router';
import {  MaterialModule } from '../../src/modules/angular-materials';
import { ExerciciosComponent } from './exercicios/exercicios.component';
import { TreinosComponent } from './treinos/treinos.component';
import { PersonalizadoComponent } from './personalizado/personalizado.component';
import { HistoricoComponent } from './historico/historico.component';
import { MaisComponent } from './mais/mais.component';
import { PerfilAlunoComponent } from './perfil-aluno/perfil-aluno.component';
import { PerfilAlunoPopUpComponent } from './perfil-aluno-pop-up/perfil-aluno-pop-up.component';
import { MenuResponsiveComponent } from './menu-responsive/menu-responsive.component';
import {AppComponent} from './app.component'
import { HomeComponent } from './home/home.component';
import { MudarFotografiaPopupComponent } from './mudar-fotografia-popup/mudar-fotografia-popup.component';
import { LoginComponent } from './login/login.component';
import { CadastroComponent } from './cadastro/cadastro.component';
import { RecuperarSenhaComponent } from './recuperar-senha/recuperar-senha.component';
import { LogoutComponent } from './logout/logout.component';


//import { MenuComponent } from './menu/menu.component';




const routes: Routes = [
    {path: 'exercicios', component:ExerciciosComponent} ,
    {path: 'treinos', component:TreinosComponent},
    {path: 'personalizado', component:PersonalizadoComponent} ,
    {path: 'historico', component:HistoricoComponent} ,
    {path: 'mais', component:MaisComponent},
    {path: 'perfil#aluno', component:PerfilAlunoComponent},
    {path: 'editarperfilaluno', component:PerfilAlunoPopUpComponent},
    {path: 'menu', component:MenuResponsiveComponent},
    {path: '', component:LoginComponent},
    {path: 'home', component:HomeComponent},
    {path: 'registro', component:CadastroComponent},
    {path: 'editar-foto', component:MudarFotografiaPopupComponent},
    {path: 'recuperar#senha', component:RecuperarSenhaComponent},
    {path: 'logout', component:LogoutComponent}





    
  ];
  
  @NgModule({
    imports: [RouterModule.forRoot(routes),
        MaterialModule,
  
    ],
    exports: [RouterModule,MaterialModule]
  })


  export class AppRoutingModule { }



